<template>
	<v-content>
		<v-navigation-drawer permanent app dark clipped width="350">
			<component
				v-if="appData.grouped.sales"
				:is="legendOptions.component"
				:legendData="legendOptions"
			/>
			<component
				v-if="appData.grouped.arc"
				:is="arcOptions.component"
				:legendData="arcOptions"
			/>
		</v-navigation-drawer>
		<v-container class="pa-0">
			<div id="map-container" :style="`height:${config.Story ? storyHeight : height}`">
				<SimpleMap />
			</div>
		</v-container>
	</v-content>
</template>

<script>
	import Legend from "../core_components/Legends/Legend.vue";
	import ArcLegend from "../core_components/Legends/ArcLegend.vue";
	import SimpleMap from "../core_components/Maps/SimpleMap.vue";
	import TimeSlider from "../core_components/UI-Other/TimeSlider.vue";

	import { mapState } from "vuex";

	export default {
		components: {
			Legend,
			ArcLegend,
			SimpleMap,
			TimeSlider
		},
		name: "Viewer",
		data: () => ({
			storyHeight: "calc(100vh - 118px)",
			height: "calc(100vh - 70px)",
			legendOptions: {
				title: "Sales Legend",
				component: "Legend",
				id: "sales"
			},
			arcOptions: {
				title: "Arc Legend",
				component: "ArcLegend",
				id: "arc"
			}
		}),
		computed: {
			...mapState({
				config: state => state.config,
				appData: state => state.dataLoader.appData
			})
		},
		beforeCreate() {
			this.getHeight;
		}
	};
</script>

<style lang="scss">
	#map-container {
		width: 100vw;
	}
</style>
